import date from './date'
import object from './object'
import data from './data'
import store from '@/store/index'
class uitl {
  constructor(){
  }
  static get date(){
    return date;
  }
  static get object(){
    return object;
  }
  static get data(){
    return data;
  }
  static hasLoginComponents(perm) {
    var userperms = store.getters.GETUSERPERMS;
    return perm.includes(perm);
  }
  static formreset(ref) {
    ref.resetFields();
  }
  static formclearValidate(ref) {
    ref.clearValidate();
  }
};
export default uitl;
export {
  date,
  object,
  data,
}
