/**
 * @author 肖清宇
 * @param {object} rules 当前使用的验证规则对象
 * @param {string} value 当前输入栏已输入值
 * @param {string} callback 回调显示错误函数 callback参数为空时，表示无错误，简要报错可查看以下自定义验证规则
 */

/**
 * rules额外引用
 *
 * @param {string} name 验证字段的名称，如验证某名称，可返回某名称是否为空，name默认为数据，
 * @param {string} state 查重访问的子目录
 * @param {string} findName 查重访问的查询定义标识，与后台协调
 * @param {string} len 当前数据的限定长度
 * @param {string} value 确认密码验证规则中的密码值，与规则函数中的value形参为两个含义
 */
let times;
import { data } from '@/lib/utils';
import axios from '../axios';

let valdateFun = {
  //数据查重
  findDuplicateData(rules, value, callback) {
    clearTimeout(times)
    let name = rules.name || '数据';
    let url = `${rules.state||''}/inspectData.action`;
    new Promise((resolve, reject) => {
      times = setTimeout(() => {
        axios.post(url, data.formatter({
          data: value,
          name: rules.findName
        })).then((success) => {
          if (success.bool) {
            resolve();
          } else {
            reject(new Error(`${name}查重${success.msg}`))
          }
        }).catch((error) => {
          reject(new Error(`网络出错，请稍后再试`));
        })
      }, 500);
    }).then(success => {
      callback()
    }).catch(error => {
      callback(error)
    })
  },
  //电话规则
  tel(rules, value, callback) {
    let bool = (value.length == 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value));
    if (!bool) {
      return callback(new Error('请输入正确格式的手机号'));
    } else {
      callback();
    }
  },
  //数字规则
  isnumber(rules, value, callback) {
    let name = rules.name || "数据";
    let bool = (/[0-9]+$/.test(value))
    if (bool) {
      callback();
    } else {
      callback(new Error(`${name}应为数字`));
    }
  },
  //数字和长度规则
  lenAndnumber(rules, value, callback) {
    let name = rules.name || "数据";
    let len = rules.len || 0;
    let bool = ((len > 0 ? value.length == len : true) && /[0-9]+$/.test(value))
    if (bool) {
      callback();
    } else {
      callback(new Error(`${name}应为${len}位数字`));
    }
  },
  //中文长度规则
  cnLength(rules, value, callback) {
    let name = rules.name || "数据";
    let len = rules.len || 0;
    let valLen = value.replace(/[\u0391-\uFFE5]/g, "aa").length;
    if (len >= valLen) {
      callback();
    } else {
      callback(new Error(`${name}长度不可超过${len}位,汉字为两位`))
    }
  },
  //确认密码验证规则
  validateRepass(rules, value, callback) {
    let passvalue = rules.value || "";
    if (passvalue === value) {
      callback();
    } else {
      callback(new Error('两次密码不符，请检查后输入'))
    }
  },
   //密码验证规则
  password(rules, value, callback) {
    let bool = /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?![,\!\.#%'\+\*\-:;^_`]+$)[,\!\.#%'\+\*\-:;^_`0-9A-Za-z]{6,16}$/.test(value);
    if (!bool) {
      callback(new Error('密码应为字母、数字和符号两种及以上的组合，6-16个字符'));
    } else {
      callback();
    }
  },
  //身份证验证规则
  idnumber(rules, value, callback) {
    let bool = /(^\d{110}$)|(^\d{112}$)|(^\d{17}(\d|X|x)$)/.test(value);
    if (!bool) {
      callback(new Error('身份证号格式不正确'));
    } else {
      callback();
    }
  },
  //名称类型验证规则
  name(rules, value, callback) {
    let bool = /^[\u4e00-\u9fa10a-zA-Z]+$/.test(value);
    if (!bool) {
      callback(new Error('姓名只能包含汉字或字母'));
    } else {
      callback();
    }
  }
}

export default valdateFun;
