import { Message } from 'element-ui';
import NProgress from 'nprogress';
import 'nprogress/nprogress.css';
import Vue from 'vue';
import Router from 'vue-router';
import utils from '../lib/utils';
import store from '../store';
// console.log(process.env.NODE_ENV)
const _import = require('./_import_production');// + process.env.NODE_ENV
Vue.use(Router)
const routeOut=[{
  path: '/login',
  name: 'Login',
  component: _import('Login'),
  meta:{
    title:"登录"
  }
},
{
  path: '/rigster',
  name: 'rigster',
  component:_import('rigster'),
  meta:{
    title:"注册"
  }
}];
const routerDefaultMap= [
  
  {
    path: '/',
    name: 'main',
    component:_import('main'),
    meta:{
      title:"AI_BD云平台",
      requireAuth:true
    },
    redirect:"index",
    children:[
      {
        path:"index",
        name:"index",
        component:_import("mainIndex"),     
        meta:{
          title:'首页',
          requireAuth:true
        }
      },
      {
        path:"/userindex",
        name:"userindex",
        component:_import('user/user'),
        meta: {
          // 添加该字段，表示进入这个路由是需要登录的
           requireAuth: true,  
           title:"个人中心"
          },
      },
      {
        path:"functionManager",
        name:"functionManager",
        component:_import("Administration/functionManager"),
        meta:{
          title:'功能分配',
          requireAuth:true
        }
      },
      {
        path:"roleManager",
        name:"roleManager",
        component:_import("Administration/roleManager"),     
        meta:{
          title:'角色管理',
          requireAuth:true
        }
      },
      {
        path:"userManager",
        name:"userManager",
        component:_import("Administration/userManager"),
        meta:{
          title:'用户管理',
          requireAuth:true
        }
      },
      {
        path:"applicationMan",
        name:"applicationMan",
        component:_import("Administration/applicationManager"),
        meta:{
          title:'应用系统管理',
          requireAuth:true
        }
      },
    ]
  },
];
let router= new Router({
  // base: __dirname,
  routes:[...routerDefaultMap,...routeOut]
})

router.beforeEach((to, from, next) => {
  // 判断该路由是否需要登录权限
  NProgress.start();
  if (to.meta.requireAuth) {
    // 通过vuex state获取当前的token是否存在
    if (!utils.object.isEmpty(store.state.user.user) && store.state.user.islogin) {
      var userpage = store.state.user.userPage;
      var index = 0;
      
      if (to.fullPath == '/') {
        index+=1;
      } else {
        for (var i = 0; i < userpage.length; i++) {
          if (userpage[i].list != null) {
            for (var j = 0; j < userpage[i].list.length; j++) {
              if ("/"+ userpage[i].list[j].pageInf.url == to.fullPath) {
                index += 1;
              }
            }
          }
        }
        if (to.fullPath == '/userindex'||to.fullPath=='/index') {
          index = 1;
        }
        if (index > 0) {
          next();
        } else {
          Message({
            type:"warning",
            message:"前往网页不存在或角色权限不足",
            onClose:()=>{
              next({
                path: '/main'
              });
            }
          })
        }
      }
    } else {
      Message({
        type:"warning",
        message:"帐号未登录",
        onClose:()=>{
          store.commit("set_is_login", false);
          store.commit("remove_user");
          next({
            path: '/login',
            //  query: {redirect: to.fullPath}  // 将跳转的路由path作为参数，登录成功后跳转到该路由
          });
        }
      })
    }
  } else {
    next();
  }
})

router.afterEach((to,form)=>{
  NProgress.done();
})

export default router;

