<template>
  <div id="app">
    <!-- <back></back> -->
    <transition mode="out-in" name="el-fade-in-linear">
      <router-view></router-view>
    </transition>
  </div>
</template>


<script>
export default {
  data(){
    return {
      websocket:null
    }
  },
  components:{
    back:()=>import('@/components/back.vue')
  },
  mounted() {
    this.$socket.emit("token","ok")
  },
  watch:{
    $route(news){
      let title=news.meta.title;
      title=(title==undefined?this.$store.getters.get_default_title:title);
      this.$store.commit("update_web_title",title);
      document.getElementsByTagName("head")[0].getElementsByTagName("title")[0].innerText=title;
    }
  },
};
</script>
<style>
body{
  margin: 0;
  padding: 0;
}
#app{
  /* background-image:url('assets/image/bg1.jpg'); */
  background-color: #0f405a;
  min-height: 100vh;
}
</style>

