   /**
    *
    *
    * @param {number} val
    * @returns
    * @memberof date
    */

   let addZero = (val) => {
     if (val < 10) {
       return "0" + val;
     } else {
       return val;
     }
   }
   class date {
     /**
      *
      *
      * @param {string} date
      * @returns
      * @memberof date
      */
     static completion(date) {
       let iDate = new Date(date);
       let hour = iDate.getHours();
       let minuite = Number(iDate.getMinutes());
       let second = Number(iDate.getSeconds());
       return hour + ":" + (minuite < 10 ? 0 + "" + minuite : minuite) + ":" + (second < 10 ? 0 + "" + second : second);
     }
     /**
      *
      *
      * @param  {string} value
      * @param {string} type="yyyyMMdd"
      * @returns
      * @memberof date
      */
     static formatter(value, type = "yyyy年MM月dd日") {
       //为小于10的数字加0
       let date = new Date(value);
       if (date.getTime() == 0) {
         return null;
       }
       let year = addZero(date.getFullYear());
       let month = addZero(date.getMonth() + 1);
       let day = addZero(date.getDate());
       let hour = addZero(date.getHours());
       let minuite = addZero(date.getMinutes());
       let second = addZero(date.getSeconds());
       let formatterMap = new Map([
         ['yyyy年MM月dd日', () => `${year}年${month}月${day}日`],
         ['HH:mm:ss', () => {
           return `${hour}:${minuite}:${second}`
         }]
       ]);
       return formatterMap.get(type)();
     }
   }
   export default date;
