import utils, {
  object
} from '@/lib/utils'
import {
  stat
} from 'fs';
import api from '@/api'
/*定义数据*/
const state = {
  user: {},
  userPage: [],
  islogin: false,
  userPerms: [],
}
//定义方法
const mutations = {
  update_user: (state, user) => {
    state.user = user;
  },
  set_is_login: (state, status) => {
    state.islogin = status;
  },
  update_user_page: (state, userpage) => {
    state.userPage = userpage;
  },
  remove_user: (state) => {
    state.user = {};
    state.userPage.length = 0;
    state.islogin = false;
    state.userPerms.length = 0;
  },
  update_user_perms: (state, userPerms) => {
    state.userPerms = userPerms;
  }
}
const getters = {
  get_user: (state) => {
    return state.user;
  },
  get_user_page: (state) => {
    return state.userPage;
  },
  get_user_perms: (state) => {
    return state.userPerms;
  }
}
const actions = {
  get_user_menu: (store) => {
    api.getuserMenu().then(success => {
      var res = success;
      if (res.bool) {
        store.commit("update_user_page", res.data.userPageList);
        store.commit("update_user_perms", res.data.perms);
      }
    }).catch(error => {
      console.log(error);
    })
  },
  login_user: async (store, data) => {
    return await api.getData('/login.action', data).then(success => {
      if (success.bool) {
        store.commit("update_user", {
          id: success.data.id,
        });
        store.commit("set_is_login", true);
        store.dispatch('get_user_menu');
        return Promise.resolve(true);
      } else {
        return Promise.reject(false);
      }
    }).catch(error => {
      console.log('object :', error);
      return Promise.reject(false);
    }) //utils.data.formatter(user)
  },
}
const store = {
  state,
  getters,
  actions,
  mutations
}
export default store;