import Vue from "vue";
const state = {
  openTab: [{
    name: "index",
    title:"首页",
    url: "/index"
  }], //所有打开的路由
  activeIndex: '/index' //激活状态
}
const mutations = {
  add_tabs(state, data) {
    state.openTab.push(data);
  },
  // 删除tabs
  delete_tabs(state, route) {
    let index = 0;
    for (let option of state.openTab) {
      if (option.url === route) {
        break;
      }
      index++;
    }
    state.openTab.splice(index, 1);
  },
  // 设置当前激活的tab
  set_active_index(state, index) {
    state.activeIndex = index;
  },
  set_tabs(state,data){
      if(data!=null){
        state.openTab=data.openTab;
        state.activeIndex=data.activeIndex;
      }
  }
}
const actions = {
}
const getters = {
  get_tabs_openTab: (state) => {
    return state.openTab;
  },
  get_tabs_activeIndex: (state) => {
    return state.activeIndex;
  },
}
const Rouerstate = {
  state,
  mutations,
  actions,
  getters
}

export default Rouerstate;
