import '@/plugins/axios'
import store from '@/store'
import {
  MessageBox,
  Message
} from 'element-ui'
let api = {
  async getuserMenu() {
    return await axios.post("/selectUserPage.action");
  },
  async getTableData(url, data) {
    return await axios.post(url, data);
  },
  async getData(url, data) {
    return await axios.post(url, data);
  },
  async deleteRequset(url, data) {
    let bool = false;
    await MessageBox.confirm('该操作将会删除当前选中数据,是否继续', '删除提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消',
      type: 'warning'
    }).then(async () => {
      await axios.post(url, data).then(success => {
        bool = success.bool;
        if (bool) {
          Message({
            type: 'success',
            message: '删除成功!'
          });
          store.dispatch("get_table_data");
        }
      })
    }).catch(() => {
      Message({
        type: 'info',
        message: '已取消删除'
      });
      bool = false;
    });
    return bool;
  },
  async opreateRequset(url, data, title = "操作") {
    let bool = false;
   await axios.post(url, data).then((success) => {
      bool = success.bool;
      store.dispatch("get_table_data");
      bool ? Message({
        type: 'success',
        message: `${title}成功!`
      }) : "";
    })
    return bool;
  }
};

export default api;
