import api from '@/api'
import utils from '@/lib/utils'
import {
  init
} from 'echarts';

const state = {
  url: "", //当前请求地址
  parems: {}, //请求附加信息
  data: [], //表格数据数组
  pagesizes: [5, 10, 15], //可选择每页显示条数数组
  active: 1, //当前页
  pagesize: 5, //每页显示数据条数
  listTotal: 0, //总条数
  count: 5, //页码可显示数，即页码数组长度
}
const mutations = {
  update_url(state, val = '') {
    state.url = val;
  },
  update_parems(state, val = []) {
    state.parems = val;
  },
  update_active(state, val = 1) {
    state.active = val;
  },
  update_data(state, val = []) {
    state.data = val;
  },
  update_pagesize(state, val) {
    state.pagesize = val;
  },
  update_listtotal(state, val = 0) {
    state.listTotal = val;
  },
  update_count(state, val = 5) {
    state.count = val;
  },
  re_init_page(state) {
    state = {
      data: [], //表格数据数组
      pagesizes: [5, 10, 15], //可选择每页显示条数数组
      active: 1, //当前页
      pagesize: 5, //每页显示数据条数
      listTotal: 0, //总条数
      count: 5, //页码可显示数，即页码数组长度
    }
  },
}
const actions = {
  get_table_data: async (store,{url,parems}) => {
    // {
    //   page: {
    //     active: state.active,
    //     pagelist: state.pagesize
    //   },
    //   state: { ...state.parems
    //   }  
    // }
    await api.getTableData(url, utils.data.formatter(parems, {
      active: state.active,
      pagelist: state.pagesize
    })).then(success => {
      store.commit("update_listtotal", success.data.listTotal);
      store.commit("update_data", success.data.lists);
    }).catch(error => {})
  }
}
const getters = {
  get_active: (state) => {
    return state.active;
  },
  get_pagesizes: (state) => {
    return state.pagesizes;
  },
  get_pagesize: (state) => {
    return state.pagesize;
  },
  get_data: (state) => {
    return state.data;
  },
  get_count: (state) => {
    return state.count;
  },
  get_listTotal: (state) => {
    return state.listTotal;
  },
}
const page = {
  state,
  mutations,
  actions,
  getters
}
export default page;
