class obj {
  static isEmpty(obj){
    for (var key in obj) {
      return false;
    }
    return true;
  }
  static copy(obj){
    var data;
    if (obj instanceof Object) {
      data = {};
    }
    if (obj instanceof Array) {
      data = [];
    }
    for (var key in obj) {
      if (typeof obj[key] == "object") {
        data[key] = this.copy(obj[key]);
      } else {
        data[key] = obj[key];
      }
    }
    return data;
  }
}
export default obj;
