import Vue from 'vue'
import Vuex from 'vuex'
import user from './modules/user'
import tabs from './modules/tabs'
import page from './modules/page'
import vuexLocalstorage from 'vuex-localstorage';
Vue.use(Vuex)
const store = new Vuex.Store({
  modules: {
    user,
    tabs,
    page
  },
  state:{
    defaulttitle:"AI_BD智能云平台",
    webtitle:"",
  },
  mutations:{
    update_web_title(state, title){
      state.webtitle=title;
    }
  },
  getters:{
    get_web_title(state){
      return state.webtitle;
    },
    get_default_title(state){
      return state.defaulttitle;
    }
  },
  plugins:[
    vuexLocalstorage({
      namespace:'ai-bd-store',
      initalState:{},
      expires:24*60*60,//以秒为单位
    })
  ]
})
export default store;
